:root {
  --font-work-sans: "Work Sans", sans-serif;
}

body {
  font-family: var(--font-work-sans);
}

:root {
  --white: hsl(0, 0%, 100%);
  --light-pink: hsl(275, 100%, 97%);
  --pale-purple: hsl(292, 16%, 49%);
  --dark-purple: hsl(292, 42%, 14%);
  --pink: hsl(281, 83%, 54%);
  --background: var(--light-pink);
  --border: var(--light-pink);
  --text: var(--dark-purple);
  --faq-text: var(--pale-purple);
  --faq-hover: var(--pink);
  --shadow-color: rgba(80, 0, 118, 0.1);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--background);
  background-image: url(../images/background-pattern-mobile.svg);
  background-size: contain;
  background-repeat: repeat-x;
  background-position: top center;
  color: var(--text);
}
@media (min-width: 40.5em) {
  body {
    background-image: url(../images/background-pattern-desktop.svg);
    background-size: auto 20rem;
  }
}

hr {
  border: 0;
  border-top: 1px solid var(--light-pink);
  height: 0;
  opacity: 1;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1,
h2 {
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 2rem;
}

@media (min-width: 40.5em) {
  h1 {
    margin: 0;
    font-size: 3.5rem;
    font-weight: 700;
  }
}
.container {
  margin: 8.875rem 1.5rem 1.5rem;
  padding: 1.5rem;
  max-width: 37.5rem;
  border-radius: 0.5rem;
  background-color: var(--white);
  box-shadow: 0 2rem 3.5rem 0 var(--shadow-color);
}
@media (min-width: 40.5em) {
  .container {
    padding: 2.5rem;
    margin: 10.4375rem auto 1.5rem;
    border-radius: 1rem;
  }
}

.header {
  display: flex;
  align-items: center;
  margin-bottom: 1.5rem;
  align-self: stretch;
}
.header img {
  height: 1.5rem;
  width: 1.5rem;
  margin-right: 1.5rem;
}
@media (min-width: 40.5em) {
  .header {
    margin-bottom: 2rem;
  }
  .header img {
    height: 2.5rem;
    width: 2.5rem;
  }
}

.accordion__item:not(:first-child) {
  padding-top: 1.25rem;
}
.accordion__item:not(:last-child) {
  padding-bottom: 1.25rem;
  border-bottom: 1px solid var(--light-pink);
}
@media (min-width: 40.5em) {
  .accordion__item:not(:first-child) {
    padding-top: 1.5rem;
  }
  .accordion__item:not(:last-child) {
    padding-bottom: 1.5rem;
  }
}
.accordion__trigger {
  margin: 0;
  padding: 0;
  width: 100%;
  padding-right: 3.4375rem;
  color: var(--text);
  text-align: left;
  font-family: inherit;
  font-size: 1rem;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  background-color: transparent;
  border: none;
  cursor: pointer;
  position: relative;
  transition: all 0.25s ease-in-out;
}
.accordion__trigger::after {
  content: "";
  display: block;
  height: 1.875rem;
  width: 1.875rem;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  background-image: url(../images/icon-plus.svg);
  background-repeat: no-repeat;
}
.accordion__trigger[aria-expanded=true] {
  margin-bottom: 1.5rem;
}
.accordion__trigger[aria-expanded=true]::after {
  background-image: url(../images/icon-minus.svg);
}
@media (min-width: 40.5em) {
  .accordion__trigger {
    font-size: 1.125rem;
  }
}
.accordion__trigger:hover {
  color: var(--faq-hover);
}
.accordion__panel {
  margin: 0;
  font-size: 0.875rem;
  line-height: 150%;
  font-weight: 400;
  color: var(--faq-text);
}
@media (min-width: 40.5em) {
  .accordion__panel {
    font-size: 1rem;
  }
}